import Axios from 'axios';
import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';

export default function Login({ saveUserData }) {
	let link = useNavigate();
	const [error, setError] = useState('');
	const [user, setUser] = useState({
		email: '',
		password: '',
	});

	function getUserData(e) {
		let userData = { ...user };
		userData[e.target.name] = e.target.value;
		setUser(userData);
	}
	async function sendUserInfoToAPI() {
		let { data } = await Axios.post(
			`https://route-movies-api.vercel.app/signin 
`,
			user
		);
		if (data.message === 'success') {
			setError(data.message);
			localStorage.setItem('userToken', data.token);
			saveUserData();
			link('/');
		} else {
			setError(data.message);
		}
	}

	function submitForm(e) {
		e.preventDefault();
		sendUserInfoToAPI();
	}

	return (
		<div className='bg-dark'>
			<div className='container vh-100 text-center d-flex justify-content-center align-items-center flex-wrap'>
				<form
					action=''
					className='border rounded-2 w-75 p-5'
					onSubmit={submitForm}>
					<h1 className='mb-5 text-warning'>
						Log in to <br /> Just Watch
					</h1>

					<div className='row'>
						<div className='col-md-12'>
							<div className='form-floating mb-4'>
								<input
									onChange={getUserData}
									type='email'
									className='form-control'
									id='email'
									name='email'
								/>
								<label htmlFor='email'>Email address</label>
							</div>

							<div className='form-floating mb-4'>
								<input
									onChange={getUserData}
									type='password'
									className='form-control'
									id='password'
									name='password'
								/>
								<label htmlFor='password'>Password</label>
							</div>
							<p className='fw-bold alert alert-warning border-0 py-0 mt-1'>
								{error}
							</p>
							<button
								type='submet'
								className='btn btn-success px-5 py-2'>
								Login
							</button>
							<h5 className='text-white text-start'>
								Don't have an account{' '}
								<Link
									to='/register'
									className='text-warning text-decoration-underline'>
									Sign up
								</Link>
							</h5>
						</div>
					</div>
				</form>
			</div>
		</div>
	);
}
