import Axios from 'axios';
import Joi from 'joi';
import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import Hambozo from './register.module.css';

export default function Register() {
	let link = useNavigate();
	const [error, setError] = useState('');
	const [isloading, setisLoading] = useState(false);
	const [validationError, setValidationError] = useState([]);
	const [user, setUser] = useState({
		first_name: '',
		last_name: '',
		email: '',
		age: 0,
		password: '',
	});

	function getUserData(e) {
		let userData = { ...user };
		userData[e.target.name] = e.target.value;
		setUser(userData);
	}
	async function sendUserInfoToAPI(e) {
		let { data } = await Axios.post(
			`https://route-movies-api.vercel.app/signup
`,
			user
		);
		if (data.message == 'success') {
			setError(data.message);
			setisLoading(false);
			// link('/');
		} else {
			setError(data.message);
			setisLoading(false);
		}
	}

	function submitForm(e) {
		e.preventDefault();
		setisLoading(true);
		let validation = validateForm();
		if (validation.error) {
			setisLoading(false);
			setValidationError(validation.error.details);
			console.log(validationError);
		} else {
			sendUserInfoToAPI();
		}
	}

	function validateForm() {
		{
			let scheme = Joi.object({
				first_name: Joi.string().min(3).max(20).required(),
				last_name: Joi.string().min(3).max(20).required(),
				email: Joi.string()
					.email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } })
					.required(),
				age: Joi.number().min(1).max(100).required(),
				password: Joi.string().min(8).max(25).required(),
			});
			return scheme.validate(user, { abortEarly: false });
		}
	}
	return (
		<div className={Hambozo.registerSection}>
			<div className='container vh-100 text-center d-flex justify-content-center align-items-center flex-wrap'>
				<form
					action=''
					className='border rounded-2 w-75 p-5'
					onSubmit={submitForm}>
					<img
						src='http://routeegypt.com/GameOver/assets/images/logo.png'
						className='w-25'
						alt=''
					/>
					<h1 className={Hambozo.h1}>Create Your Account!</h1>
					<div className='row'>
						<div className='col-md-6'>
							<div className='form-floating'>
								<input
									onChange={getUserData}
									type='text'
									className='form-control'
									id='first_name'
									name='first_name'
								/>
								<label htmlFor='first_name'>First Name</label>
							</div>
							<p className='fw-bold alert alert-warning border-0 py-0 mt-1'>
								{validationError.map((err) => {
									if (err.context.label == 'first_name') {
										return 'First Name length must be between 3 to 20 character';
									}
								})}
							</p>
						</div>
						<div className='col-md-6'>
							<div className='form-floating'>
								<input
									onChange={getUserData}
									type='text'
									className='form-control'
									id='last_name'
									name='last_name'
								/>
								<label htmlFor='last_name'>Last Name</label>
							</div>
							<p className='fw-bold alert alert-warning border-0 py-0 mt-1'>
								{validationError.map((err) => {
									if (err.context.label == 'last_name') {
										return 'Last Name length must be between 3 to 20 character';
									}
								})}
							</p>
						</div>

						<div className='col-md-12'>
							<div className='form-floating mb-4'>
								<input
									onChange={getUserData}
									type='email'
									className='form-control'
									id='email'
									name='email'
								/>
								<label htmlFor='email'>Email address</label>
								<p className='fw-bold alert alert-warning border-0 py-0 mt-1'>
									{validationError.map((err) => {
										if (err.context.label == 'email') {
											return 'Email is required';
										}
									})}
								</p>
							</div>

							<div className='form-floating mb-4'>
								<input
									onChange={getUserData}
									type='number'
									className='form-control'
									id='age'
									name='age'
								/>
								<label htmlFor='age'>age</label>
								<p className='fw-bold alert alert-warning border-0 py-0 mt-1'>
									{validationError.map((err) => {
										if (err.context.label == 'age') {
											return 'Age is required';
										}
									})}
								</p>
							</div>

							<div className='form-floating mb-4'>
								<input
									onChange={getUserData}
									type='password'
									className='form-control'
									id='password'
									name='password'
								/>
								<label htmlFor='password'>Password</label>
								<p className='fw-bold alert alert-warning border-0 py-0 mt-1'>
									{validationError.map((err) => {
										if (err.context.label == 'password') {
											return 'Password is required min length is 8 characters';
										}
									})}
								</p>
							</div>
							<p className='text-black fw-bold'>{error}</p>
							<button type='submet' className='btn btn-success'>
								{isloading ? (
									<i className='fas fa-spinner fa-spin'></i>
								) : (
									'Submit Form'
								)}
							</button>
							<h5 className='text-white text-start'>
								Already have an account{' '}
								<Link
									to='/login'
									className='text-warning text-decoration-underline'>
									Login
								</Link>
							</h5>
						</div>
					</div>
				</form>
			</div>
		</div>
	);
}
