import React from 'react';
import './Profile.css';

export default function Profile({ userData }) {
	let { first_name, last_name, email, age, _id } = userData;
	console.log(userData);
	return (
		<div className=' bg-dark text-center  vh-100 d-flex justify-content-center align-items-center'>
			<div className='container'>
				<div className='profile-container py-5'>
					<div className='profileIcon'>
						<img
							src='https://icon-library.com/images/no-image-icon/no-image-icon-0.jpg'
							alt=''
						/>
					</div>
					<div className='userinfo w-75 m-auto text-start p-5 text-'>
						<h3>
							User Id: <span className='text-white'>{_id}</span>
						</h3>
						<h3>
							User Name:{' '}
							<span className='text-white'>
								{first_name}, {last_name}
							</span>
						</h3>
						<h3>
							User Email: <span className='text-white'>{email}</span>
						</h3>
						<h3>
							User Age: <span className='text-white'>{age}</span>
						</h3>
					</div>
				</div>
			</div>
		</div>
	);
}
