import Axios from 'axios';
import React, { useEffect, useState } from 'react';
import ReactPaginate from 'react-paginate';
import { useNavigate } from 'react-router-dom';
import MediaCard from '../../components/movie/MediaCard';

const Tvshows = () => {
	let [movies, setMovies] = useState([]);
	const navigate = useNavigate();

	async function getApi(pageNumber) {
		let {
			data: { results },
		} = await Axios.get(
			`https://api.themoviedb.org/3/trending/tv/week?api_key=67fef309e68cd33fae156a0dfd535e0f&page=${pageNumber}`
		);
		setMovies(results);
	}

	async function Paginate({ selected }) {
		const pageNumber = selected + 1;
		let {
			data: { results },
		} = await Axios.get(
			`https://api.themoviedb.org/3/trending/tv/week?api_key=67fef309e68cd33fae156a0dfd535e0f&page=${pageNumber}`
		);
		setMovies(results);
		navigate(`/tv/page/${pageNumber}`);
	}

	useEffect(() => {
		getApi(1);
	}, []);

	return (
		<>
			<div className=' bg-dark pt-5 text-white overflow-hidden'>
				<div className='container py-5'>
					<div className='row py-5 '>
						<div className='col-md-4 text-white d-flex justify-content-center align-items-center  '>
							<div className=' w-100 '>
								<div className='brbr w-25 mb-4 '></div>
								<h2 className=' my-4'>
									Trending <br /> Tv-Shows <br /> To Watch Now
								</h2>
								<h4 className='text-muted'>
									Most Watched Tv-Shows Now
								</h4>
								<div className='brbr w-100  mt-4'></div>
							</div>
						</div>
						{movies.map((item, index) => (
							<MediaCard item={item} key={index} />
						))}

						<ReactPaginate
							previousLabel={'Previout'}
							nextLabel={'Next'}
							breakLabel={'...'}
							pageCount={20}
							marginPagesDisplayed={3}
							pageRangeDisplayed={5}
							onPageChange={Paginate}
							containerClassName={'pagination justify-content-center'}
							pageClassName={'page-item'}
							pageLinkClassName={'page-link'}
							previousClassName={'page-item'}
							previousLinkClassName={'page-link'}
							nextClassName={'page-item'}
							nextLinkClassName={'page-link'}
							breakClassName={'page-item'}
							breakLinkClassName={'page-link'}
							activeClassName={'active'}
						/>
					</div>
				</div>
			</div>
		</>
	);
};

export default Tvshows;
