import React from 'react';
import { Link } from 'react-router-dom';
import './Navbar.css';
import { NavLink } from 'react-router-dom';

export default function Navbar({ userData, logOut }) {
	return (
		<>
			<nav
				className='navbar navbar-expand-lg bg-dark navbar-dark fixed-top mb-5 shadow overflow-hidden'
				id='mainNav'>
				<div className='container'>
					<div className='logo'>
						<Link
							className='navbar-brand text-warning'
							to='/'
							title='Logo'>
							Just Watch
						</Link>
					</div>
					<button
						className='navbar-toggler'
						type='button'
						data-bs-toggle='collapse'
						data-bs-target='#navbarSupportedContent'
						aria-controls='navbarSupportedContent'
						aria-expanded='false'
						aria-label='Toggle navigation'>
						<span className='navbar-toggler-icon'></span>
					</button>
					<div
						className='collapse navbar-collapse text-uppercase nav-links overflow-hidden'
						id='navbarSupportedContent'>
						<ul className='navbar-nav d-flex  justify-content-center mb-2 mb-lg-0 w-100'>
							{userData ? (
								<>
									<li className='nav-item py-1 ps-2 position-relative'>
										<NavLink
											className='nav-link uline '
											to='/'
											title='Home Page'
											data-bs-target='#navbarSupportedContent'>
											Home
										</NavLink>
									</li>
									<li className='nav-item py-1 ps-2 position-relative'>
										<NavLink
											className='nav-link uline'
											title='Movies Page'
											to='movies/page/1'>
											Movies
										</NavLink>
									</li>
									<li className='nav-item py-1 ps-2 position-relative'>
										<NavLink
											className='nav-link uline'
											to='tv/page/1'
											title='Tv Shows Page'>
											Tv Shows
										</NavLink>
									</li>
									<li className='nav-item py-1 ps-2 position-relative'>
										<NavLink
											className='nav-link uline'
											to='contact'
											title='Contact Page'>
											Contact
										</NavLink>
									</li>
								</>
							) : (
								''
							)}
						</ul>
						<ul className='navbar-nav'>
							{!userData ? (
								<>
									<li className='nav-item py-1 ps-2 position-relative'>
										<Link
											className='nav-link uline'
											to='login'
											title='Login Page'>
											logIn
										</Link>
									</li>
									<li className='nav-item py-1 ps-2 position-relative'>
										<Link
											className='nav-link uline'
											to='register'
											title='Register Page'>
											Register
										</Link>
									</li>
								</>
							) : (
								<ul className='d-flex justify-content-center align-items-center'>
									<Link
										className='nav-link uline'
										to='profile'
										title='Profile Page'>
										<i className='fa-regular fa-user text-white m-0 p-0 d-block'></i>
									</Link>
									<p className='text-white text-capitalize fw-bold m-0 p-0 '>
										Welcome,{userData.first_name}
									</p>
									<li
										className='nav-item py-1 ps-2 position-relative'
										onClick={() => {
											logOut();
										}}>
										<Link
											className='nav-link uline text-danger'
											to='login'
											title='LogOut Page'>
											LogOut
										</Link>
									</li>
								</ul>
							)}
						</ul>
					</div>
				</div>
			</nav>
		</>
	);
}
