import Axios from 'axios';
import React, { useEffect, useState } from 'react';
import MediaCard from '../../components/movie/MediaCard';
import './movies.css';
// Motion
import { motion } from 'framer-motion';
//Varionts
import { fadeIn } from '../../variants';

export default function Movies() {
	let [movies, setMovies] = useState([]);
	let [tv, setTv] = useState([]);

	async function getApi(mediaType, callback) {
		let { data } = await Axios.get(
			`https://api.themoviedb.org/3/trending/${mediaType}/week?api_key=67fef309e68cd33fae156a0dfd535e0f`
		);
		callback(data.results);
	}

	useEffect(() => {
		getApi('movie', setMovies);
		getApi('tv', setTv);
	}, []);

	return (
		<>
			<div className=' bg-dark pt-5 text-white overflow-hidden'>
				<div className='container py-5'>
					<div className='row py-5 '>
						<motion.div
							variants={fadeIn('right', 0.2)}
							initial='hidden'
							whileInView={'show'}
							viewport={{ once: true, amount: 0.5 }}
							className='col-md-4 text-white d-flex justify-content-center align-items-center  '>
							<div className=' w-100 '>
								<div className='brbr w-25 mb-4 '></div>
								<h2 className=' my-4'>
									Trending <br /> Movies <br /> To Watch Now
								</h2>
								<h4 className='text-muted'>
									Most Watched Movies By Days
								</h4>
								<div className='brbr w-100  mt-4'></div>
							</div>
						</motion.div>
						{movies.slice(0, 10).map((item, index) => (
							<MediaCard item={item} key={index} />
						))}
					</div>

					<div className='row py-5'>
						<motion.div
							variants={fadeIn('right', 0.3)}
							initial='hidden'
							whileInView={'show'}
							viewport={{ once: true, amount: 0.3 }}
							className='col-md-4 text-white d-flex justify-content-center align-items-center '>
							<div className=' w-100'>
								<div className='brbr w-25 mb-4 '></div>
								<h2 className=' my-4'>
									Trending <br /> TV Shows
									<br /> To Watch Now
								</h2>
								<h4 className='text-muted'>
									Most Watched TV Shows By Days
								</h4>
								<div className='brbr w-100  mt-4'></div>
							</div>
						</motion.div>
						{tv.slice(0, 10).map((item, index) => (
							<MediaCard item={item} key={index} />
						))}
					</div>

					{/* <div className='row py-5'>
						<div className='col-md-4 text-white d-flex justify-content-center align-items-center '>
							<div className=' w-100'>
								<div className='brbr w-25 mb-4 '></div>
								<h2 className=' my-4'>
									Trending <br /> Celebrities <br /> To Watch Now
								</h2>
								<h4 className='text-muted'>
									Most Watched Celebrities By Days
								</h4>
								<div className='brbr w-100  mt-4'></div>
							</div>
						</div>
						{person
							.filter((person) => person.profile_path !== null)
							.slice(0, 10)
							.map((item, index) => (
								<MediaCard item={item} key={index} />
							))}
					</div> */}
				</div>
			</div>
		</>
	);
}
