import React, { useState } from 'react';
import { useRef } from 'react';
import './Contact.css';
import emailjs from '@emailjs/browser';

export default function Contact() {
	const form = useRef();
	const [from, setForm] = useState(null);
	const [fromError, setFormError] = useState(null);

	const sendEmail = (e) => {
		e.preventDefault();

		emailjs
			.sendForm(
				'service_8syilmb',
				'template_flmgg2n',
				form.current,
				'bcpzyfNkFeVFqGY8b'
			)
			.then(
				(result) => {
					setForm(true);
					e.target.reset();
				},
				(error) => {
					setFormError(true);
				}
			);
	};
	return (
		<>
			<section
				className='contact text-center bg-dark min-vh-100'
				id='contact'>
				<div className='container py-2'>
					<header className='pt-5 '>
						<div className='row'>
							<div className='col-sm-12'>
								<div className='title'>
									<h3 className='w-bold text-capitalize position-relative'>
										Contact Me.
									</h3>
								</div>
							</div>
						</div>
					</header>

					<div className='contact-info mb-4'>
						<div className='row'>
							<div className='col-lg-4'>
								<div className='item p-5 rounded-5 border'>
									<span className='icon'>
										<i
											className='fa fa-location-arrow d-flex justify-content-center align-items-center rounded-circle mb-4'
											aria-hidden='true'></i>
									</span>
									<h4 className='text-white fw-bold'>Address</h4>
									<p className='item-info'>Alexandria - Egypt</p>
								</div>
							</div>

							<div className='col-lg-4 '>
								<div className='item p-5 rounded-5 border'>
									<a
										href='mailto:ebrahimamra69@gmail.com'
										className='icon text-decoration-none text-dark'>
										<i
											className='fa fa-envelope d-flex justify-content-center align-items-center rounded-circle mb-4'
											aria-hidden='true'></i>
									</a>

									<h4 className='text-white fw-bold'>Email</h4>
									<p className='item-info'>ebrahimamra69@gmail.com</p>
								</div>
							</div>

							<div className='col-lg-4'>
								<div className='item p-5 rounded-5 border'>
									<span className='icon'>
										<a
											href='tel:+201020307846'
											className='icon text-decoration-none text-dark'>
											<i
												className='fa fa-phone d-flex justify-content-center align-items-center rounded-circle mb-4'
												aria-hidden='true'></i>
										</a>
									</span>
									<h4 className='text-white fw-bold'>Phone</h4>
									<p className='item-info'>+20 010 2030 7846</p>
								</div>
							</div>
						</div>
					</div>

					<form
						action=''
						className=' text-start border px-5 pb-5 rounded-5'
						onSubmit={sendEmail}
						ref={form}>
						<div className='row'>
							<h2 className='text-warning text-center mt-5 mb-4'>
								Send me a message
							</h2>
							<div className='.col-md-12 mb-4'>
								<div className='row'>
									<div className='col-md-6 mb-3'>
										<input
											type='text'
											name='name'
											className='form-control bg-light'
											placeholder='Name'
											required
										/>
									</div>
									<div className='col-md-6 '>
										<input
											type='text'
											name='email'
											className='form-control bg-light'
											placeholder='Email'
											required
										/>
									</div>
								</div>
							</div>

							<div className='col-md-12 mb-4'>
								<textarea
									className='form-control bg-light'
									name='message'
									rows='8'
									placeholder='Message'
									required=''></textarea>
							</div>
						</div>
						{from && (
							<p className='text-success mx-auto'>
								Thank you. Your message was sent successfully
							</p>
						)}
						{fromError && (
							<p className='text-danger'>
								Sorry. Your message was not send try again later
							</p>
						)}
						<input
							type='submit'
							value='Submit'
							className='btn  btn-success py-2 px-5'
						/>
					</form>
				</div>
			</section>
		</>
	);
}
