import Axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import MediaCard from '../../components/movie/MediaCard';
import { motion } from 'framer-motion';
//Varionts
import { fadeIn } from '../../variants';

export default function ItemDetails() {
	let { id, media_type } = useParams();
	const [itemDetails, setItemDetails] = useState({});
	const [similar, setSimilar] = useState([]);

	async function getItemDetails(id, media_type) {
		let { data } = await Axios.get(
			`https://api.themoviedb.org/3/${media_type}/${id}?api_key=67fef309e68cd33fae156a0dfd535e0f&language=en-US`
		);
		setItemDetails(data);
	}

	async function getSimilar(id, media_type) {
		let { data } = await Axios.get(
			`https://api.themoviedb.org/3/${media_type}/${id}/similar?api_key=67fef309e68cd33fae156a0dfd535e0f&language=en-US&page=1`
		);
		setSimilar(data.results);
	}
	useEffect(() => {
		getItemDetails(id, media_type);
		getSimilar(id, media_type);
	}, [id, media_type]);

	return (
		<>
			<div className='bg-dark'>
				<div className='container py-5 overflow-hidden'>
					<div className='row py-5'>
						<motion.div
							variants={fadeIn('down', 0.3)}
							initial='hidden'
							whileInView={'show'}
							viewport={{ once: true, amount: 0.3 }}
							className='col-md-4'>
							<img
								src={
									'https://image.tmdb.org/t/p/w500' +
									itemDetails.poster_path
								}
								className='w-100'
								alt=''
							/>
							<img
								src={
									'https://image.tmdb.org/t/p/w500' +
									itemDetails.profile_path
								}
								className='w-100'
								alt=''
							/>
						</motion.div>
						<motion.div
							variants={fadeIn('left', 0.3)}
							initial='hidden'
							whileInView={'show'}
							viewport={{ once: true, amount: 0.3 }}
							className='col-md-8 text-white'>
							<h2 className='text-white mb-3'>{itemDetails.title}</h2>
							<h2 className='text-white mb-3'>{itemDetails.name}</h2>
							<h4 className='text-muted h3 mb-5'>
								{itemDetails.tagline}
							</h4>
							<ul className='d-flex mx-0 p-0 mb-4'>
								{itemDetails.genres?.map((item, index) => (
									<li
										key={index}
										className='list-unstyled me-3 bg-info p-2 rounded fw-bold'>
										{item.name}
									</li>
								))}
							</ul>
							<h5 className='h4 mb-4'>
								Vote : {itemDetails.vote_average?.toFixed(1)}
							</h5>
							<h5 className='h4 mb-4'>
								Vote Count : {itemDetails.vote_count}
							</h5>
							<h5 className='h4 mb-4'>
								Popularity : {itemDetails.popularity?.toFixed(0)}
							</h5>
							<h5 className='h4 mb-4'>
								Release Date : {itemDetails.release_date}
								{itemDetails.first_air_date}
							</h5>
							<h4 className='text-muted mb-4'>{itemDetails.overview}</h4>
						</motion.div>
						<div className='col-md-12'>
							<div className='row'>
								<h2 className='text-capitalize h1 ms-5 text-warning'>
									Similar {media_type}
									{media_type == 'movie' && 's'}
									{media_type == 'tv' && '-Shows'}
								</h2>
								{similar.map((item, index) => (
									<MediaCard item={item} key={index} />
								))}
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}
